<template>
	<div class="guide-main">
		<van-loading v-if="loading" type="spinner" size="24" />
		<ul v-else class="data-list">
			<div v-if="nodata" class="no-data"></div>
			<template v-else>
				<li class="no-active" v-for="(item, i) in guide" :key="i">
					<div class="item-img">
						<van-image :src="icon">
							<template v-slot:loading>
								<van-loading type="spinner" size="20" />
							</template>
						</van-image>
					</div>
					<div class="item-content">
						<div class="title">{{item.fileName}}</div>
						<div class="time">{{formatDate(item.insertTime)}}</div>
						<div class="more-action">
							<a @click="onPreview(item)">预览</a>
							<a @click="onDownload(item)">下载</a>
						</div>
					</div>
				</li>
			</template>
		</ul>
	</div>
</template>

<script>
	const icon = require('../../assets/images/file.png')
	export default {
		name: 'guide',
		data() {
			return {
				icon: icon,
				loading: true,
				nodata: false,
				guide: []
			}
		},
		mounted() {
			this.getData()
		},
		methods: {
			//日期格式化
			formatDate(date) {
				date = this.$Utils.toDate(date)
				return this.$Utils.dateFtt('yyyy-MM-dd hh:mm:ss', date)
			},
			//获取数据
			async getData() {
				let params = {
					cultivateId: this.$route.query.id
				}
				let res = await this.$Request.get('/cultivate/queryGuideByWeChat.do', params)
				this.loading = false
				if(res && res.code === 0) {
					let data = res.guide || []
					this.nodata = !data.length
					this.guide = data
				}
			},
			// 预览
			onPreview(item) {
				window.open(CONFIG.API + "/cultivate/previewOrDown.do?filePath=" + item.filePath + "&fileName=" + item.fileName)
			},
			// 下载
			onDownload(item) {
				window.location.href = CONFIG.API + "/cultivate/previewOrDown.do?filePath=" + item.filePath + "&fileName=" + item.fileName + "&type=download"
			}
		}
	}
</script>

<style lang="scss" scoped>
	.more-action {
		text-align: right;
		a {
			margin-left: 20px;
			color: #1aad19;
			cursor: pointer;
		}
	}
</style>


